@import url(https://fonts.googleapis.com/css2?family=Almarai&display=swap);
body {
  margin:0 auto;
  font-family: 'Almarai',sans-serif !important;
}


input[type="text"]
{
    font-size:15px !important;
    font-family: inherit !important;
    font-weight: 500;
}
.center-all {
  margin: 0 !important;
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
  justify-content:space-around !important;
}
.accept-button{
     width:140px !important;
     font-size: medium !important;
    }
.qr-button{
    width: 50px !important;
}


.nav{
    width: 100%;
    background: #ffffff !important;
    z-index: 100;
    font-family: inherit !important;
   
    }
.icon-btn{
    position:relative;
    z-index:100;
    margin-left: -30px;
    padding-top: 10px;
}
i{
    color: grey;
}
.input-field{
    padding: 13px 40px !important;
}
.background{
    margin-top: -30px;
}

.stores-table{
    width: 100% !important;
    margin-top: 10px;
    margin-bottom: 50px;
    font-family: inherit;
}

.main-div{
    width: 90% !important;
}

.page-title{
color: grey;
font-family: inherit;
padding-top: 10px;
padding-bottom: 10px;

}
.log-container{
    margin: 0 !important;
    position: absolute !important;
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
    justify-content:space-around !important;
  
}

.log-form{
    position: absolute;
    width: 800px !important;
    padding: 5%  10%!important;
    
}

input{
    text-align: right !important;
    
}

.log-btn{
width: 150px !important;

}

center{
    margin-top: 30px;
}

.title{
    font-family: inherit !important;
    font-size: 20px !important;
   
}
.text{
    font-size: 16px !important;
    text-align: right;
    line-height: 25px !important;
    
}
.button {
    background-color: #F53838 !important;
    color: #fff !important;
    width: 170px;
    padding: 8px 10px !important;
    font-size: 18px;
    font-weight: 500 !important;
    font-family: inherit !important;
    margin-top: 15px !important;
}
.button:hover {
    background-color: #d16161 !important;
    color: #fff !important;
}
.checkbox {
   align-items: right !important;
}

.success {
    background-color: rgba(169, 255, 173, 0.2) !important;
    padding: 50px 50px !important;
    color: #595959 !important;
    border-radius: 15px !important;
    border: 1px solid #27D42E !important;
}
.code{
    font-weight: 900 !important;
}
.terms {
    text-decoration: underline !important;
    cursor: pointer;
    text-align:right !important;
}
.term-list{
    /* overflow-y: scroll; */
    text-align:right;
    font-size: 17px;
    height: 200px;
}
.modal {
    top : '50%';
    left : '50%';
    right: 'auto';
    bottom : 'auto';
    margin-right : '-50%';
}
@media only screen and (max-width: 600px) {
    .term-list{
        overflow-y: scroll;
    }
  }
li {
    padding: 5px 0px;
}
.term-header {
    font-weight: bold;
    font-size: 24px;
    text-align : right;
    margin: 10px 0px 20px 0px;
}
.term-desc {
    font-weight: bold;
    font-size: 19px;
    text-align : right;
}
.checkbox {
    text-align : right;
    direction: rtl;
}
.btn-terms :hover{
cursor: pointer !important;
padding: 10px 30px !important;
}

.reg-container {
     margin: 50px !important;
}

.required:after {
    content:" *";
    color: red;
}


.content{ 
    width: 100% !important;
    height: 100% !important;
    position:  absolute !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-top: 0;
    margin-bottom: 0;
   
    }

section{
   padding-top: 0 !important; 
   height: 100% !important;
  
}    

section div{
    box-shadow: rgba(0, 255, 255, 255) 0px 0px 0px 3px !important;
    padding-top: -80px !important;
    visibility: hidden;
    
    
}
.qr-reader{
    position: absolute;
    background-color: rgba(255, 0, 0, 0);
    width: 100%;
    height: 100%;
 
}
.border{
    /* box-shadow: rgba(255, 255, 255, 255) 0px 0px 0px 3px inset;
    border-radius: 25px; */

    background:
    linear-gradient(to right, white 3px, transparent 3px) 0 0,
    linear-gradient(to right, white 3px, transparent 3px) 0 100%,
    linear-gradient(to left, white 3px, transparent 3px) 100% 0,
    linear-gradient(to left, white 3px, transparent 3px) 100% 100%,
    linear-gradient(to bottom, white 3px, transparent 3px) 0 0,
    linear-gradient(to bottom, white 3px, transparent 3px) 100% 0,
    linear-gradient(to top, white 3px, transparent 3px) 0 100%,
    linear-gradient(to top, white 3px, transparent 3px) 100% 100%;

  background-repeat: no-repeat;
  background-size: 20px 20px;

    position: absolute;
    height: 300px;
    width: 300px;
    z-index: 100;
    margin: 0 ;
    position: absolute !important;
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
}
